import { Component, Input, OnInit } from '@angular/core'

@Component({
    selector: 'app-header',
    templateUrl: 'header.component.html'
})
export class HeaderComponent {
    @Input() type: string

    setType () {
        return (this.type === 'transparent') ? 'navbar-transparent' : ''
    }
}


